<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <el-tabs class="finance-tabs" :span="24" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="info">
          <ul :data="cashData">
            <li><h3></h3><span>{{cashData.account}}</span></li>
            <li><h3></h3><span>{{cashData.total_account}}</span></li>
            <li><h3></h3> <span>{{cashData.frozen_account}}</span></li>
          </ul>
          <div><el-button type="primary" plain size="medium" @click="showCashForm"> </el-button></div>
          <hr/>
          <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
              <el-button type="success" size="medium" @click.native="addFormVisible = true"></el-button>
              <span class="title"></span>
              <el-select v-model="filters.status" clearable placeholder="" size="medium" @change="handleSearch" style="width: 160px;">
                <el-option
                  v-for="item in opt1"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form>
          </el-col>
          <el-table :data="tableBank" @selection-change="selsChange" border stripe highlight-current-row style="width:100%" element-loading-text="...">
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column type="index" label="" width="50"></el-table-column>
            <el-table-column prop="bank_number" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="bank_name" label=""></el-table-column>
            <el-table-column prop="sub_bank" label=""></el-table-column>
            <el-table-column label="" min-width="40">
              <template slot-scope="scope">
                <el-tooltip v-if="!scope.row.bank_file_url ==''" placement="top" effect="light">
                  <div slot="content"><img :src="scope.row.imgView" alt=""></div>
                  <el-button type="text"></el-button>
                </el-tooltip>
                <el-button v-if="scope.row.bank_file_url ==''" type="text"></el-button>
              </template>
            </el-table-column>
            <el-table-column prop="status" label="" :formatter="audit" min-width="50"></el-table-column>
            <el-table-column prop="remark" label="" :formatter="formatRemark"></el-table-column>
            <!--<el-table-column prop="updated" label="" :formatter="fotmatUpdated"></el-table-column>-->
            <el-table-column label="" min-width="50">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="editCard(scope.$index,scope.row)"></el-button>
                <el-button type="text" size="small" @click="handDel(scope.$index,scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label="" name="record">
          <el-table :data="withDrawList" border stripe highlight-current-row style="width:100%" element-loading-text="...">
            <el-table-column type="index" label="" width="50"></el-table-column>
            <el-table-column prop="bank_name" label=""></el-table-column>
            <el-table-column prop="sub_bank" label=""></el-table-column>
            <el-table-column prop="bank_number" label=""></el-table-column>
            <el-table-column prop="account" label=""></el-table-column>
            <el-table-column prop="bank_people_name" label=""></el-table-column>
            <el-table-column prop="status" label="" :formatter="withDrawAudit" min-width="50"></el-table-column>
            <el-table-column prop="remark" label="" :formatter="formatRemark"></el-table-column>
            <el-table-column prop="audit_time" label="" :formatter="fotmatdate1"></el-table-column>
            <el-table-column prop="create_time" label="" :formatter="fotmatdate2"></el-table-column>
          </el-table>
          <div class="elPagination">
            <el-pagination background layout="total, sizes, prev, pager, next"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="recordCurrentPage"
                     :page-sizes="[30, 60, 100]"
                     :page-size="recordSize"
                     :total="recordTotals">
            </el-pagination>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="list">
          <el-table :data="fianceListObj" border stripe highlight-current-row style="width:100%" element-loading-text="...">
            <el-table-column type="index" label="" width="80"></el-table-column>
            <el-table-column prop="type" label="" :formatter="fianceInfo"></el-table-column>
            <el-table-column prop="account" label=""></el-table-column>
            <el-table-column prop="pre_account" label=""></el-table-column>
            <el-table-column prop="next_account" label=""></el-table-column>
            <el-table-column prop="account" label=""></el-table-column>
            <el-table-column prop="remark" label="" :formatter="formatRemark" min-width="100"></el-table-column>
            <el-table-column prop="create_time" label="" :formatter="fianceListDate"></el-table-column>
          </el-table>
          <div class="elPagination">
            <el-pagination background layout="total, sizes, prev, pager, next"
                     @size-change="fianceSizeChange"
                     @current-change="fianceCurrentChange"
                     :current-page="fianceCurrentPage"
                     :page-sizes="[30, 50, 80]"
                     :page-size="fianceSize"
                     :total="fianceTotals">
            </el-pagination>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-col>
    <!--  -->
    <el-dialog title="" :visible.sync ="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="110px" :rules="addFormRules" ref="addForm">
        <el-form-item label="">
          <el-upload class="avatar-uploader" accept="image/*" ref="imgRef"
                     action="upload.action"
                     data="upload.data"
                     :show-file-list="false"
                     :action="upload.action"
                     :data="upload.data"
                     :on-change="addUpOnChange"
                     :on-success="addUpIconOnSuccess" :before-upload="beforeAvatarUpload">
            <img v-if="imgPreview" :src="imgPreview" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="bank_number">
          <el-input v-model="addForm.bank_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bank_name">
          <el-input v-model="addForm.bank_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sub_bank">
          <el-input v-model="addForm.sub_bank"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="110px" :rules="addFormRules" ref="editForm">
        <el-form-item label="" prop="bank_number">
          <el-input v-model="editForm.bank_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bank_name">
          <el-input v-model="editForm.bank_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sub_bank">
          <el-input v-model="editForm.sub_bank"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload class="avatar-uploader" accept="image/*" ref="editImgRef"
                     action="upload.action"
                     data="upload.data"
                     :show-file-list="false"
                     :action="upload.action"
                     :data="upload.data"
                     :on-change="editUpOnChange"
                     :on-success="editUpIconOnSuccess" :before-upload="beforeAvatarUpload">
            <img v-if="editPreview" :src="editPreview" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="cashFormVisible" :close-on-click-modal="false" class="cashForm">
      <el-form :model="cashForm" label-width="120px" :rules="cashFormRules" ref="cashForm">
        <!--<el-form-item label="" prop="media_bank_id">-->
          <!--<el-select v-model="cashForm.media_bank_id" placeholder="">-->
            <!--<el-option v-for="item in bankObj" :key="item.media_bank_id" :label="item.account_bank" :value="item.media_bank_id"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="" prop="id">
          <el-select clearable placeholder="" v-model="cashForm.id" size="large">
            <el-option
              v-for="item in accounts"
              :key="item.id"
              :label="item.bank_name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="account">
          <!--<el-input-number v-model="cashForm.account" :step="100" :min="100"></el-input-number>-->
          <el-input v-model="cashForm.account" :step="100" :min="100" max="10000" style="width: 217px;"></el-input>
        </el-form-item>
      </el-form>
      <p><span>100.0</span></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="cashFormVisible = false"></el-button>
        <el-button type="primary" @click.native="cashSubmit" :loading="cashLoading"></el-button>
      </div>
    </el-dialog>

  </el-row>
</template>
<script>
  import API from '../api/apis'
  import util from '../common/util'
  export default{
    data(){
      var checkMoney = (rule, value, callback) => {
        if(!value){
          return callback(new Error(''));
        }else if(value < 0){
          return callback(new Error(''));
        }else if(value > 100000){
          return callback(new Error('10'));
        }else {
          callback();
        }
      }
      return {
        opt1: [{
          value: '0',
          label: ''
        },{
          value: '1',
          label: ''
        },{
          value: '2',
          label: ''
        },{
          value: '3',
          label: ''
        }],
        activeName: 'info',
        tabSelect: [],
        cashData:{
          amount:'00.00',
          total_account:'00.00',
          frozen_account:'00.00'
        },
        tableBank:[],
        page: 1,
        size: 10,
        totals: 0,
        filters: {
          status: ''
        },
        addFormVisible: false,
        editFormVisible: false,
        cashFormVisible: false,
        cashLoading: false,
        accounts: [],
        fianceListObj: [],
        cashForm: {
          account: '',
          id: ''
        },
        addForm:{
          name:'',
          bank_number:'',
          bank_name:'',
          sub_bank: '',
          bank_file_id: ''
        },
        imgPreview: '',
        editPreview: '',
        upload: {
          action: API.uploadFile,
          data: {}
        },
        cashFormRules: {
          account: [
            //{ required: true, message: '', trigger: 'blur' },
            {type: 'string',required: true, validator: checkMoney, trigger: 'change'}
          ],
          id: [
            {type:'number', required: true, message: '', trigger: 'blur' },
          ],
        },
        addFormRules: {
          bank_number: [
            { required: true, message: '', trigger: 'blur' },
            { min: 18, max: 18, message: '18', trigger: 'blur'}
          ],
          bank_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          name:[
            { required: true, message:'', trigger:'blur'}
          ],
          sub_bank:[
            { required: true, message:'', trigger:'blur'}
          ],
          //bank_file_id:[{required: true, message:'', trigger:'change'}],
        },
        addLoading:false,
        editForm:{
          id: '',
          name:'',
          bank_number:'',
          bank_name:'',
          sub_bank: '',
          bank_file_id: ''
        },
        editLoading:false,
        withDrawList: [],
        recordPage: 1,
        recordSize: 10,
        recordCurrentPage: 1,
        recordTotals: 0,
        fiancePage: 1,
        fianceSize: 10,
        fianceCurrentPage: 1,
        fianceTotals: 0
      }
    },
    methods: {
      audit: function (row, colum) {
        return (row.status == null) ? '' : row.status === 0 ? ' ' : row.status === 1 ?''
          : row.status === 2 ?'': '-';
      },
      formatRemark: function (row, colum) {
        return (row.remark == null || row.remark == '') ? '' : row.remark;
      },
      withDrawAudit(row,colum){
        return (row.status == null) ? '' : row.status === 0 ? ' ' : row.status === 1 ?''
          : row.status === 2 ?'': '-';
      },
      fotmatdate1(row,colum){
        return (row.audit_time == null || row.audit_time == '') ? '' : util.formatDate.format(row.audit_time);
      },
      fotmatdate2(row,colum){
        return (row.create_time == null || row.create_time == '') ? '' : util.formatDate.format(row.create_time);
      },
      handleSearch(value){
        this.filters.status = value;
        this.initData();
      },
      fianceListDate(row, colum){
        return (row.create_time == null || '') ? '' : util.formatDate.format(row.create_time);
      },
      selsChange(sels){
        //console.log(sels);
        this.tabSelect = sels;
      },
      fianceInfo(row, colum){
        return (row.type == null) ? '' : row.type === 1 ? ' ' : row.status === 2 ?'' : '-';
      },
      showCashForm(){
        this.cashFormVisible = true;
        this.getAccountList();
      },
      handleClick(tab, event) {
        console.log(tab, event);
        if(tab.paneName == 'record'){
          this.recordList();
        }else if(tab.paneName == 'info'){
          this.accountInfo();
        }else if(tab.paneName == 'list'){
          this.fianceList();
        }
      },
      addSubmit(){
        this.$refs.addForm.validate((valid) =>{
          if(valid){
            this.$confirm('','',{type: 'warning'}).then(() => {
              this.addLoading = true;
              let users = JSON.parse(window.sessionStorage.getItem('access-user'));
              let param = {
                user_id: users.user_id,
                token: users.token,
                bank_number:this.addForm.bank_number,
                bank_name:this.addForm.bank_name,
                name:this.addForm.name,
                sub_bank:this.addForm.sub_bank,
                bank_file_id:this.addForm.bank_file_id
              };
              API.bankCardAdd(param).then((rs) => {
                if(rs.code === 0){
                  this.$message({
                    message:'',
                    type:'success'
                  });
                  this.$refs['addForm'].resetFields();
                  this.addLoading = false;
                  this.addFormVisible = false;
                  this.initData();
                }else {
                  this.$message.warning({showClose: true, message: rs.message, duration: 2500});
                  //this.$refs['addForm'].resetFields();
                  this.addLoading = false;
                  this.addFormVisible = true;
                }
              }).catch((err) => {
                console.log(err);
                this.addLoading = false;
                this.$message.error({showClose: true, message: '' || err, duration: 2500});
              })
            });
          }else{
            this.$message('');
            return false;
          }
        })
      },
      editCard(i,row){
        this.editFormVisible = true;
        this.editForm.id = row.id;
        this.editPreview = API.baseImage + row.bank_file_url;
        this.editForm = Object.assign({}, row);
        console.log(this.editForm)
      },
      handDel(index,row){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          id: row.id,
          user_id: users.user_id,
          token: users.token
        };
        that.$confirm('', '', {confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          API.financeDelete(params).then(function(rs){
            console.log(rs);
            if(rs.code === 0){
              that.$message({type: 'success',showClose: true, message: '', duration: 2000});
              that.initData();
            }
          },function(err){
            that.$message.error({message: rs.message ||err.toString(), duration: 2000});
          }).catch((err) => {
            console.log(err);
            that.$message({type:'error', showClose: true, message: '', duration: 2000});
          })
        }).catch((err) => {
          //this.$message({showClose: true, message: '' || err.toString() , duration: 3000});
        })
      },
      editSubmit(){
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {confirmButtonText: '',
              cancelButtonText: '', type: 'warning'}).then(() => {
              this.editLoading = true;
              let users = JSON.parse(window.sessionStorage.getItem('access-user'));
              let param = {
                id: this.editForm.id,
                user_id: users.user_id,
                token: users.token,
                bank_number: this.editForm.bank_number,
                bank_name: this.editForm.bank_name,
                name: this.editForm.name,
                sub_bank: this.editForm.sub_bank,
                bank_file_id: this.editForm.bank_file_id
              };
              API.bankCardEdit(param).then((rs) => {
                if (rs.code === 0) {
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.$refs['editForm'].resetFields();
                  this.editFormVisible = false;
                  this.editLoading = false;
                  this.initData();
                } else {
                  this.$message.warning({showClose: true, message: rs.message, duration: 2500});
                  //this.$refs['editForm'].resetFields();
                  this.editLoading = false;
                  this.editFormVisible = true;
                }
              }).catch((err) => {
                console.log(err);
                this.editLoading = false;
                this.$message.error({showClose: true, message: '' || err, duration: 2500});
              })
            });
          } else {
            this.$message('');
            return false;
          }
        })
      },
      cashSubmit(){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          user_id: users.user_id,
          bank_id: that.cashForm.id,
          token: users.token,
          account: that.cashForm.account
        };
        that.$refs.cashForm.validate((valid) => {
          if(valid){
            this.$confirm('', '', {confirmButtonText: '',
              cancelButtonText: '', type: 'warning'}).then(() => {
              API.userWithdraw(params).then(function(rs){
                console.log(rs);
                that.cashLoading = true;
                if(parseInt(rs.code) === 0){
                  that.$message({message: '', type: 'success', duration: 3000});
                  that.cashFormVisible = false;
                  that.cashLoading = false;
                  that.$refs['cashForm'].resetFields();
                }else {
                  that.cashFormVisible = true;
                  that.cashLoading = false;
                  that.$message({showClose: true, message: rs.message, duration: 3000});
                }
              }).catch((err) => {
                console.log(err);
                that.cashLoading = false;
                that.$message.error({showClose: true, message: '', duration: 3000});
              });
            }).catch(() => {})
          }else {
            return false;
          }
        })

      },
      deleteCard(i,row){},
      isDefault(i,row){},
      commonOnChange(file, type){
        if(file.status == 'ready'){
          console.log("ready")
          let users = JSON.parse(window.sessionStorage.getItem('access-user'));
          this.upload.data = {
            user_id: users.user_id,
            token: users.token,
            type: 3
            //file: file.raw
          };
          if(type == 'add'){
            this.$nextTick(() => {
              this.$refs.imgRef.submit();
            });
          }else if(type == 'edit'){
            this.$nextTick(() => {
              this.$refs.editImgRef.submit();
            });
          }
          console.log(this.upload.data);
        }else if(file.status == 'fail'){
          this.$message.error("")
        }
      },
      addUpOnChange(file, fileList){
        console.log("change")
        this.imgPreview = URL.createObjectURL(file.raw);
        this.commonOnChange(file, 'add');
      },
      addUpIconOnSuccess(res,file,list){
        console.log("icon success")
        if(res.code === 0){
          this.addForm.bank_file_id = res.data.id;
          this.$message.success("");
        }else{
          this.$message.warning(res.message);
        }
      },
      editUpOnChange(file, fileList){
        console.log("change")
        this.editPreview = URL.createObjectURL(file.raw);
        this.commonOnChange(file, 'edit');
      },
      editUpIconOnSuccess(res, file, ls){
        console.log("success")
        if(res.code === 0){
          this.editForm.bank_file_id = res.data.id;
          this.$message.success("");
        }else{
          this.$message.warning(res.message);
        }
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.warning(' JPGPNG ');
        }
        if (!isLt2M) {
          this.$message.warning(' 2MB');
        }
        return isJPG && isLt2M;
      },
      accountInfo(){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          user_id: users.user_id,
          token: users.token
        };
        API.financeInfo(params).then(function(rs){
          console.log(rs);
          if(parseInt(rs.code) === 0){
            that.cashData = rs.data;
          }else {
            that.$message({showClose: true, message: rs.message, duration: 3000});
          }
        }).catch((err) => {
          console.log(err);
          //that.loading = false;
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      },
      recordList(){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          user_id: users.user_id,
          token: users.token,
          page: that.recordPage,
          size: that.recordSize
        };
        API.financeWithdrawLs(params).then(function(rs){
          console.log(rs);
          if(parseInt(rs.code) === 0){
            that.withDrawList = rs.data.rows;
            that.recordTotals = rs.data.total;
          }else {
            that.$message({showClose: true, message: rs.message, duration: 3000});
          }
        }).catch((err) => {
          console.log(err);
          //that.loading = false;
          that.$message.error({showClose: true, message: '', duration: 3000});
        });
      },
      fianceList(){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          user_id: users.user_id,
          token: users.token,
          page: that.fiancePage,
          size: that.fianceSize
        };
        API.financeList(params).then(function(rs){
          console.log(rs);
          if(parseInt(rs.code) === 0){
            that.fianceListObj = rs.data.rows;
            that.fianceTotals = rs.total;
          }else {
            that.$message({showClose: true, message: rs.message, duration: 3000});
          }
        }).catch((err) => {
          console.log(err);
          that.$message({type: 'error', message: '', duration: 3000});
        });
      },
      handleSizeChange(val) {
        //console.log(` ${val} `);
        this.recordSize = val;
        this.recordList();
      },
      handleCurrentChange(val) {
        //console.log(`: ${val}`);
        this.recordPage = val;
        this.recordList();
      },
      //
      fianceSizeChange(val) {
        this.fianceSize = val;
        this.fianceList();
      },
      fianceCurrentChange(val) {
        this.fiancePage = val;
        this.fianceList();
      },
      getAccountList(){
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let opts = {
          user_id: users.user_id,
          token: users.token,
          page: 1,
          size: 20,
          status: 2
        };
        API.bankCardList(opts).then((rs) => {
          console.log(rs)
          if(rs.code === 0){
            this.accounts = rs.data.rows;
          }else {
            this.$message({message: rs.message, duration: 3000})
          }
        }).catch((err) => {
          this.$message({message: rs.message || err,showClose: true, duration: 3000})
        })
      },
      initData(){
        let that = this;
        let users = JSON.parse(window.sessionStorage.getItem('access-user'));
        let params = {
          user_id: users.user_id,
          token: users.token,
          page: that.page,
          size: that.size,
          status: that.filters.status
        };
        that.logining = true;
        API.bankCardList(params).then(function(rs){
          console.log(rs);
          let data = rs.data.rows;
          if(parseInt(rs.code) === 0){
            for(let i = 0; i< data.length; i++){
              data[i].imgView = API.baseImage + data[i].bank_file_url;
            }
            that.tableBank = data;
            that.totals = rs.data.total;
          }else {
            //that.loading = false;
            that.$message({showClose: true, message: rs.message, duration: 3000});
          }
        }).catch((err) => {
          console.log(err);
          //that.loading = false;
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      }
    },
    mounted() {
      this.accountInfo();
      this.initData();
    }
  }
</script>
<style lang="scss" scoped>
  .finance-tabs{
    padding-top: 5px;
  }
  .el-tab-pane ul {
    width: 100%;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-align: center;
  }
  .el-tab-pane ul li {
    list-style: none;
    width: 33.3%;
    float: left;
    box-sizing: border-box;
    border-right: 1px solid #ddd;
    margin: 20px 0 36px;
  }
  .el-tab-pane ul li:last-child{
    border-right: none;
  }
  .el-tab-pane ul li h3 {
    font-size: 20px;
    color: #333;
    font-weight: normal;
    margin: 10px 0;
  }
  .el-tab-pane ul li span {
    font-size: 16px;
    color: #2196f3;
  }
  hr {margin:30px 0 20px;}
  .toolbar{
    .title{padding-left: 10px}
  }
  .cashForm p {
    box-sizing: border-box;
    padding-left: 46px;
  }
</style>



